.row {
  margin-left: 20px;
  color: white;
  margin-bottom: 20px;
}

.row_posters {
  display: flex;
}

.row_poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  transition: transfrom 450ms;
  margin-right: 10px;
  margin-top: 5px;
}

.row_poster:hover {
  transform: scale(1.08);
  opacity: 1;
}

.row_posterLarge {
  max-height: 250px;
}

.row_poster:hover {
  transform: scale(1.1);
  opacity: 1;
}
